
"use client";

import { Button } from "@/components/ui/button";
import { ArrowDown } from "lucide-react";
import { useTranslations } from "@/hooks/useTranslations";

export default function HeroSection() {
  const t = useTranslations();

  const scrollToProjects = () => {
    const projectsElement = document.getElementById("projects-canvas");
    if (projectsElement) {
      projectsElement.scrollIntoView({ behavior: "smooth" });
    } else {
      // Fallback if the specific ID isn't found, try to scroll to the next major section
      const nextSection = document.querySelector('div[class*="h-[60vh]"]'); // A bit generic, but might work
      nextSection?.scrollIntoView({ behavior: "smooth"});
    }
  };

  return (
    <section className="w-full py-20 md:py-32 bg-gradient-to-b from-background to-transparent text-center">
      <div className="container mx-auto px-4">
        <h1 className="text-4xl md:text-6xl font-bold tracking-tight text-primary mb-6">
          {t.hero.title}
        </h1>
        <p className="text-lg md:text-xl text-foreground/80 max-w-3xl mx-auto mb-8">
          {t.hero.subtitle}
        </p>
        <Button 
          size="lg" 
          className="bg-primary hover:bg-primary/90 text-primary-foreground text-lg px-8 py-6 shadow-lg transform transition-transform hover:scale-105"
          onClick={scrollToProjects}
        >
          {t.hero.ctaButton} <ArrowDown className="ml-2 h-5 w-5" />
        </Button>
      </div>
    </section>
  );
}

