
"use client";

import { useState, useEffect } from 'react'; // Added useEffect
import type { Project } from '@/types/project';
import { projects as projectData } from '@/data/projects'; 
import HeroSection from './HeroSection';
import AboutSection from './AboutSection';
import SkillsSection from './SkillsSection';
import ContactSection from './ContactSection';
import ProjectsSection from './ProjectsSection'; 
import { Separator } from '@/components/ui/separator';
import ChatWidget from '@/components/chat/ChatWidget';
import ScrollToTopButton from '@/components/ScrollToTopButton';
import ImagePreviewModal from '@/components/portfolio/ImagePreviewModal';
import { useTranslations } from '@/hooks/useTranslations';


export default function PortfolioClientPage() {
  const t = useTranslations();

  const [imageToPreview, setImageToPreview] = useState<Project | null>(null);
  const [isImageModalOpen, setIsImageModalOpen] = useState(false);

  const [chatProjectContext, setChatProjectContext] = useState<string | undefined>(undefined);
  const [chatInitialMessage, setChatInitialMessage] = useState<string | undefined>(undefined);
  const [isChatWidgetOpen, setIsChatWidgetOpen] = useState(false);

  const handleViewImage = (project: Project) => {
    setImageToPreview(project);
    setIsImageModalOpen(true);
  };

  const handleCloseImageModal = () => {
    setIsImageModalOpen(false);
    setImageToPreview(null);
  };

  const handleAskAI = (project: Project) => {
    setChatProjectContext(project.chatContext || project.description);
    setChatInitialMessage(t.projectsSection.chatInitialMessage.replace('{projectTitle}', project.title));
    setIsChatWidgetOpen(true); // Open the chat widget
  };
  
  // Effect to manage body cursor style based on modal/chat states
  useEffect(() => {
    if (isImageModalOpen || isChatWidgetOpen) {
      document.body.classList.add('cursor-default-override');
    } else {
      document.body.classList.remove('cursor-default-override');
    }
    // Cleanup function to remove the class when the component unmounts or dependencies change
    return () => {
      document.body.classList.remove('cursor-default-override');
    };
  }, [isImageModalOpen, isChatWidgetOpen]); // Correctly use useEffect with dependencies


  return (
    <div className="flex-grow flex flex-col w-full items-center">
      <HeroSection />
      
      <div className="container mx-auto px-4 py-8 space-y-12">
        <ProjectsSection 
          projects={projectData} 
          onViewImage={handleViewImage}
          onAskAI={handleAskAI}
        />
        <Separator className="my-12" />
        <AboutSection />
        <Separator className="my-12" />
        <SkillsSection />
        <Separator className="my-12" />
        <ContactSection />
      </div>

      {isImageModalOpen && imageToPreview && (
        <ImagePreviewModal
          imageUrl={imageToPreview.imageUrl}
          altText={imageToPreview.title}
          isOpen={isImageModalOpen}
          onClose={handleCloseImageModal}
        />
      )}
      
      <ChatWidget
        key={isChatWidgetOpen ? 'chat-open' : 'chat-closed'} 
        startOpen={isChatWidgetOpen}
        initialMessage={chatInitialMessage}
        projectContext={chatProjectContext}
        // onOpenChange might be needed if ChatWidget controls its own visibility
        // onOpenChange={(open) => { if(!open) setIsChatWidgetOpen(false)}}
      />
      <ScrollToTopButton />
    </div>
  );
}
