
"use client";

import type { Project } from '@/types/project';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import NextImage from "next/image";
import { Button } from "@/components/ui/button";
import { Eye, Bot, ExternalLink } from "lucide-react";
import { useTranslations } from '@/hooks/useTranslations';

interface ProjectsSectionProps {
  projects: Project[];
  onViewImage: (project: Project) => void;
  onAskAI: (project: Project) => void;
}

export default function ProjectsSection({ projects, onViewImage, onAskAI }: ProjectsSectionProps) {
  const t = useTranslations();

  return (
    <section id="projects" className="py-16">
      <div className="text-center mb-12">
        <h2 className="text-3xl md:text-4xl font-bold text-primary">{t.projectsSection.title}</h2>
        <p className="text-md text-muted-foreground mt-2">{t.projectsSection.subtitle}</p>
      </div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        {projects.map((project) => (
          <Card key={project.id} className="shadow-xl bg-card/80 backdrop-blur-sm tilt-card flex flex-col">
            <CardHeader className="p-4">
              <div className="relative aspect-[16/10] w-full mb-3">
                <NextImage
                  src={project.imageUrl}
                  alt={project.title}
                  layout="fill"
                  objectFit="cover"
                  className="rounded-t-md"
                  data-ai-hint={project.dataAiHint}
                />
                 <Button
                  variant="ghost"
                  size="icon"
                  className="absolute top-2 right-2 bg-black/40 hover:bg-black/60 text-white hover:text-primary rounded-full h-9 w-9"
                  onClick={() => onViewImage(project)}
                  aria-label={t.projectsSection.viewImageAriaLabel.replace('{projectTitle}', project.title)}
                  title={t.projectsSection.viewImageAriaLabel.replace('{projectTitle}', project.title)}
                >
                  <Eye className="h-5 w-5" />
                </Button>
              </div>
              <CardTitle className="text-xl text-primary group-hover:text-primary/90 transition-colors">{project.title}</CardTitle>
            </CardHeader>
            <CardContent className="p-4 pt-0 flex-grow">
              <CardDescription className="text-sm text-card-foreground/80 line-clamp-4">
                {project.description}
              </CardDescription>
            </CardContent>
            <CardFooter className="p-4 pt-2 flex flex-col sm:flex-row gap-2 items-stretch">
              {project.link && (
                <Button asChild variant="outline" className="flex-grow border-primary/50 hover:border-primary text-primary hover:bg-primary/10">
                  <a href={project.link} target="_blank" rel="noopener noreferrer">
                    <ExternalLink className="mr-2 h-4 w-4" /> {t.projectsSection.viewProjectButton}
                  </a>
                </Button>
              )}
              <Button
                variant="outline"
                onClick={() => onAskAI(project)}
                className="flex-grow border-secondary/50 hover:border-secondary text-secondary hover:bg-secondary/10"
                aria-label={t.projectsSection.askAIAriaLabel.replace('{projectTitle}', project.title)}
                title={t.projectsSection.askAITooltip}
              >
                <Bot className="mr-2 h-4 w-4" /> {t.projectsSection.askAIButton}
              </Button>
            </CardFooter>
          </Card>
        ))}
      </div>
    </section>
  );
}
