
"use client";

import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogClose } from "@/components/ui/dialog";
import NextImage from "next/image";
import { Button } from "../ui/button";
import { X } from "lucide-react";
import { useTranslations } from "@/hooks/useTranslations";

interface ImagePreviewModalProps {
  imageUrl: string;
  altText: string;
  isOpen: boolean;
  onClose: () => void;
}

export default function ImagePreviewModal({ imageUrl, altText, isOpen, onClose }: ImagePreviewModalProps) {
  const t = useTranslations();
  if (!isOpen) return null;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[90vw] md:max-w-[70vw] lg:max-w-[60vw] xl:max-w-[50vw] p-0 overflow-hidden">
        <DialogHeader className="p-4 flex flex-row justify-between items-center border-b">
          <DialogTitle className="text-lg text-primary">{t.imagePreviewModal.titlePrefix} {altText}</DialogTitle>
          <DialogClose asChild>
            <Button variant="ghost" size="icon" aria-label={t.imagePreviewModal.closeButtonAriaLabel}>
              <X className="h-5 w-5" />
            </Button>
          </DialogClose>
        </DialogHeader>
        <div className="p-4 flex justify-center items-center bg-background/50">
          <NextImage
            src={imageUrl}
            alt={altText}
            width={1200} // Max width for the image inside modal
            height={800} // Max height
            className="rounded-md object-contain max-h-[70vh] w-auto"
            data-ai-hint="showcase image" // Generic hint, specific hints are on project data
          />
        </div>
      </DialogContent>
    </Dialog>
  );
}
